
.blue-gradient-bg {
  background: #1569b0 /* Old browsers */;
  background: -moz-linear-gradient(45deg, #1569b0 0%, #01abce 100%) /* FF3.6-15 */;
  background: -webkit-linear-gradient(45deg, #1569b0 0%,#01abce 100%) /* Chrome10-25,Safari5.1-6 */;
  background: linear-gradient(45deg, #1569b0 0%,#01abce 100%) /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */;
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1569b0', endColorstr='#01abce',GradientType=1 ) /* IE6-9 fallback on horizontal gradient */; }

@mixin list($marker-color, $accent-color) {
    font-weight: 300;
    margin-top: 30px;
    @media all and (max-width: 767px) {
      margin: 30px 0; }
    li {
      margin-bottom: 30px;
      p {
        font-size: .95em;
        display: inline-block;
        width: calc(100% - 40px);
        line-height: 1.4em;
        padding-left: 20px;
        strong {
          text-transform: uppercase;
          color: $accent-color; } }
      &::before {
        float: left;
        content: '';
        display: inline-block;
        width: 20px;
        height: 20px;
        background: transparent;
        border-right: 2px solid $marker-color;
        border-top: 2px solid $marker-color;
        transform: rotate(45deg);
        position: relative;
        top: .8em;
        @media all and (min-width: 768px) {
          top: .4em; } } } }

@mixin section-border($color) {
  border-top: solid 10px $color;
  &::before {
    content: '';
    display: inline-block;
    width: 0px;
    border-left: 15px solid transparent;
    border-top: 15px solid transparent;
    border-right: 15px solid $color;
    border-bottom: 15px solid $color;
    position: relative;
    transform: rotate(45deg);
    top: -17px;
    left: calc(50% - 15px); } }

@mixin section-regular($list-marker, $list-accent) {
  h2 {
    margin-top: 40px;
    text-transform: uppercase;
    font-weight: 300;
    font-size: 2em;
    letter-spacing: 0.05em;
    line-height: 1.15em;
    margin-bottom: 25px;
    text-align: center;
    span {
      font-weight: 600;
      font-size: inherit;
      letter-spacing: inherit; } }
  > .row {
    padding-bottom: 60px;
    img {
      max-width: 100vw;
      position: relative;
      top: -10px;
      //margin-bottom: -60px
      margin-left: -15px; }
    ul {
      @include list($list-marker, $list-accent);
      li::before {
        width: 14px;
        height: 14px;
        top: .5em; } } }
  @media all and (min-width: 768px) {
    > .row {
      padding: 30px 40px 60px 80px; } } }

@mixin extra-text($bg-color, $text-color) {
  padding: 20px;
  background: $bg-color;
  color: $text-color;
  font-weight: 400;
  @media all and (min-width: 768px) {
    border-radius: 20px; } }

@mixin rolldown-content {
  display: none;
  div {
    margin-bottom: 25px;
    text-align: center; }
  img {
    margin: 5px; }
  p {
    font-size: .85em;
    color: $brown; } }

@mixin rolldown-heading {
  text-align: center;
  margin: 35px;
  cursor: pointer;
  &:hover {
    text-decoration: underline; }
  &.active {
    text-decoration: underline; }
  i {
    font-size: .8em; } }
