@import '_colors';
@import '_reset';
@import '_mixins';

html, body {
  max-width: 100%;
  font-family: 'Roboto', sans-serif;
  div, span, p, h1, h2 {
    letter-spacing: 0.035em;
    font-size: 20px; }
  h1 {
    font-weight: 600;
    text-transform: uppercase; } }

#popup {
  display: none;
  position: fixed;
  height: 70px;
  padding: 15px 25px;
  border-radius: 5px;
  top: calc(100vh - 80px);
  left: 20px;
  width: calc(100vw - 40px);
  font-weight: 300;
  color: $white;
  text-align: center;
  background-color: rgba(0,0,0,.4);
  z-index: 3;
  p {
    font-size: 17.5px; }
  i {
    position: relative;
    left: calc(50% + 15px);
    top: -50px;
    opacity: .6;
    cursor: pointer;
    &:hover, &:active {
      opacity: 1; } } }

.upper {
  position: fixed;
  z-index: 2;
  width: 100vw;
  height: 60px;
  font-size: 20px;
  .menu_heading {
    display: inline-block;
    width: 100%;
    text-align: center;
    min-height: 60px;
    background: rgba(0,0,0,.3);
    cursor: pointer;
    span {
      color: $white;
      font-size: 1em;
      display: inline-block;
      margin: 20px 0;
      font-weight: 200;
      transition: color ease .2s;
      font-size: 20px;
      &:hover {
        color: $lightblue; } }
    i {
      transition: transform ease .3s; }
    &.active {
      border-radius: 0;
      i {
        transform: rotate(180deg); }
      & + .menu_content {
        max-height: 380px; } } }
  .menu_content {
    display: inline-block;
    max-height: 0;
    overflow-y: hidden;
    transition: max-height ease .5s;
    background: rgba(0,0,0,.3);
    border-radius: 0 0 5px 5px;
    a {
      position: relative;
      display: inline-block;
      width: 100%;
      background: rgba(0,0,0,.6);
      padding: 15px 30px 10px;
      z-index: 1;
      color: $white;
      font-weight: 300;
      cursor: pointer;
      transition: background-color ease .2s;
      font-size: 20px;
      &:last-child {
        border-radius: 0 0 5px 5px; }
      &:hover, &:active {
        text-decoration: none;
        background-color: lighten($lightblue, 8%); } } }
  &.lang {
    position: fixed;
    width: initial;
    display: inline;
    right: 0;
    padding: 20px;
    a {
      color: $white;
      cursor: pointer;
      transition: color .2s;
      &:hover, &:visited, &:active {
        text-decoration: none; } } } }

footer {
  background: $blue;
  padding-top: 25px;
  font-weight: 300;
  .row {
    background: $blue;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  > div {
    margin-bottom: 20px;
    text-align: center;
    color: $white;
    > div {
      margin: 10px 0;
      p {
        padding-left: 20px;
        margin-bottom: 20px;
        strong {
          font-weight: 400; } }
      a {
        color: $white;
        &:link, &:hover, &:active, &:visited {
          color: $white; } } } }
  .maps {
    padding: 0; }
  #map1, #map2 {
    width: 100vw;
    height: 200px;
    margin-bottom: 20px; } }

@media all and (min-width: 768px) {
  #popup {
    max-width: 385px;
    left: calc(100vw - 410px);
    p {
      font-size: 19px; } }
  footer {
    padding-left: 50px;
    padding-right: 50px;
    .row {
      flex-direction: row; }
    .maps {
      padding-left: 15px;
      padding-right: 15px; }
    #map1, #map2 {
      width: 100%; } }
  .upper {
    width: 30%;
    min-width: 300px;
    right: 10%;
    float: right;
    .menu_heading {
      border-radius: 0 0 5px 5px; }
    &.lang {
      min-width: 0;
      text-align: center;
      min-height: 60px;
      background: rgba(0,0,0,.3);
      cursor: pointer;
      border-radius: 0 0 5px 5px;
      right: 1%;
      transition: background ease .2s;
      &:hover {
        background: rgba(0,0,0,.6); } } } }
